import { Component } from 'react';
import './Portfolio.css';
import Curtains from './Curtain/Curtain.js'
import PortfolioHeader from './Header/PortfolioHeader.js'
import PortfolioLink from './Link/PortfolioLink.js'
import { motion } from 'framer-motion';
import MainPage from '../PokemonDatabase/MainPage.js'

class Portfolio extends Component {
    constructor(props) {
        super(props)
        this.state = {
            PLACEHOLDER: false,
            begin: true,
        }
        this.unmountCurtain = this.unmountCurtain.bind(this)
        this.startPokemonPage = this.startPokemonPage.bind(this)
    }
    startPokemonPage() {
        this.setState({PLACEHOLDER:true})
        console.log("ASDASD")
    }
    unmountCurtain() {
        this.setState({
            begin: false
        })
    }
    render() {
        return (
            
                this.state.PLACEHOLDER ? 
                <MainPage /> :
                <div className={"portfolio-body"}>
                    {this.state.begin?<Curtains unmount={this.unmountCurtain}/>:""}
                    <PortfolioHeader />
                    <motion.div onClick={this.startPokemonPage} style={{width: `calc(50vh - 100px)`, margin:"10px", filter: "drop-shadow(4px 4px 10px black)", height:'calc(50vh - 100px)', background: `url(${require('../PokemonDatabase/assets/pokemon-icon.png')}) 0px 0px / cover no-repeat`, cursor: "pointer"}} whileHover={{scale:1.2}}></motion.div>
                    <PortfolioLink />
                </div>
            
        )
    }
}


export default Portfolio;