import { Component } from 'react';
import './Curtain.css';

const NUM_OF_CURTAINS = 20;

class Curtains extends Component {
    render() {
        return (
            <div className={"curtain-container"}>
                {[...Array(NUM_OF_CURTAINS)].map((_item_, i) => {
                    return (
                        <div key={i} className={"curtain"} 
                            style={{
                                height: `${100/NUM_OF_CURTAINS}vh`,
                                background: "black",
                                animation: `fade ${Math.sqrt(i/10)}s cubic-bezier(0, 0.03, 0.21, 1.01)`,
                            }} 
                            onAnimationEnd={i+1==NUM_OF_CURTAINS ? this.props.unmount : null} />
                    )
                })}
            </div>
        )
    }
}

export default Curtains;