import { Component } from 'react';
import './PortfolioLink.css';
import { motion } from 'framer-motion';
import linkedIn from '../assets/linkedin.svg'
import github from '../assets/github.svg'
import resume from '../assets/resume.svg'
import resumeFile from '../assets/resume.docx'

const linkedInURL = "https://www.linkedin.com/in/william-sentosatio/" 
const githubURL = "https://github.com/povertycycle"

class PortfolioLink extends Component {
    render() {
        return (
            <div className={"portfolio-link"}>
                <div>
                    <motion.img className={'social-media-icons'} src={linkedIn} whileHover={{scale: 1.3}}
                        onClick={()=>{window.open(linkedInURL)}}
                        title={"LinkedIn"}
                    />
                </div>
                <div>
                    <motion.img style={{scale: 1.3}} className={'social-media-icons'} src={github} whileHover={{scale: 1.5}}
                        onClick={()=>{window.open(githubURL)}}
                        title={"Github"}
                    />
                </div>
                <div>
                    <motion.img className={'social-media-icons'} src={resume} whileHover={{scale: 1.3}}
                        onClick={()=>{window.open(resumeFile)}}
                        title={"Resume"}
                    />
                </div>
            </div>
        )
    }
}

export default PortfolioLink;